import * as React from 'react';
import {Form, Formik, FormikProps} from 'formik';
import {useToasts} from 'react-toast-notifications';
import * as Yup from 'yup';
import Input from '../../components/input/input';
import Button from '../../components/button/button';
import {ContactFromWrapper, ContactPageContent, ContactWrapper, InputGroup, MagicInput,} from './style';
import axios from "axios";
import {Separator} from "../about/style";

interface MyFormValues {
    firstName: string;
    email: string;
    message: string;
}

const SignupSchema = Yup.object().shape({
    firstName: Yup.string().required('Required'),
    email: Yup.string().email('Invalid email').required('Required'),
    message: Yup.string().required('Required'),
});

const Contact: React.SFC<{}> = () => {
    const {addToast} = useToasts();
    const formAction = "https://app.99inbound.com/api/e/mnvb1vhJ";

    return (
        <Formik
            initialValues={{firstName: '', email: '', message: ''}}
            onSubmit={(values: MyFormValues, actions: any) => {
                axios({
                    method: 'post',
                    url: formAction,
                    headers: {
                        'Content-Type': 'application/json',
                        'Accept': 'application/json'
                    },
                    data: {
                        name: values.firstName,
                        email: values.email,
                        message: values.message
                    }
                }).then((response) => {
                    const text = JSON.parse(response.request.response).submission_text || "Successfully submitted!";
                    actions.resetForm();
                    addToast(text, {appearance: 'success'});
                }).catch(_ => {
                    addToast("Something went wrong", {appearance: 'error'});
                }).finally(
                    () => actions.setSubmitting(false)
                )
            }}
            validationSchema={SignupSchema}
            render={({
                         handleChange,
                         values,
                         errors,
                         handleBlur,
                         touched,
                         isSubmitting,
                     }: FormikProps<MyFormValues>) => (
                <>
                    <Form>
                        <ContactWrapper>
                                <Separator/>
                                <ContactPageContent>
                                    Want to say something directly to me, without using any social network?
                                    You can use this form to email me.
                                </ContactPageContent>
                            <ContactFromWrapper>
                                <InputGroup>
                                    <Input
                                        type="text"
                                        name="firstName"
                                        value={`${values.firstName}`}
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                        label="Name"
                                        notification={`${
                                            errors.firstName && touched.firstName
                                                ? errors.firstName
                                                : ''
                                        }`}
                                    />
                                    <Input
                                        type="email"
                                        name="email"
                                        value={`${values.email}`}
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                        label="Email"
                                        notification={`${
                                            errors.email && touched.email ? errors.email : ''
                                        }`}
                                    />
                                </InputGroup>
                                <MagicInput>
                                    <Input
                                        type="checkbox"
                                        name="strong_turquoise_honeycomb_nessie"
                                        value="1"
                                        tabIndex={-1}
                                        autoComplete="no"
                                    />
                                </MagicInput>
                                <Input
                                    type="textarea"
                                    name="message"
                                    value={`${values.message}`}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    label="Message"
                                    notification={
                                        errors.message && touched.message ? errors.message : ''
                                    }
                                />
                                <Button
                                    title="Submit"
                                    type="submit"
                                    isLoading={isSubmitting ? true : false}
                                    loader="Submitting.."
                                />
                            </ContactFromWrapper>
                        </ContactWrapper>
                    </Form>
                </>
            )}
        />
    );
};

export default Contact;
