import React from 'react';
import {useStaticQuery, graphql} from 'gatsby';
import Image from 'gatsby-image';
import SocialProfile from '../../../components/social-profile/social-profile';
import {
    IntroWrapper,
    IntroImage,
    IntroTitle,
    Description,
    IntroInfo,
} from './style';

import instagramIcon from "../../../../content/social/logo-instagram.svg";
import twitterIcon from "../../../../content/social/logo-twitter.svg";
import githubIcon from "../../../../content/social/logo-github.svg";
import mastodonIcon from "../../../../content/social/logo-mastodon.svg";
import hicetnuncIcon from "../../../../content/social/logo-hicetnunc.svg";
import peepethIcon from "../../../../content/social/logo-peepeth.svg";
import deviantartIcon from "../../../../content/social/logo-deviantart.svg";
import elloIcon from "../../../../content/social/logo-ello.svg";
import showtimeIcon from "../../../../content/social/logo-showtime.svg";
import wtSocialIcon from "../../../../content/social/logo-wtsocial.svg";

type IntroProps = {};

const Intro: React.FunctionComponent<IntroProps> = () => {
    const Data = useStaticQuery(graphql`
        query {
            avatar: file(absolutePath: { regex: "/author.jpg/" }) {
                childImageSharp {
                    fluid(maxWidth: 210, maxHeight: 210, quality: 100) {
                        ...GatsbyImageSharpFluid_withWebp_tracedSVG
                    }
                }
            }
            site {
                siteMetadata {
                    author
                    about
                }
            }
        }
    `);

    const {author, about} = Data.site.siteMetadata;
    const AuthorImage = Data.avatar.childImageSharp.fluid;

    const SocialLinks = [
        {
            icon: instagramIcon,
            url: 'https://www.instagram.com/rodrigofavarete',
            tooltip: 'Instagram',
        },
        {
            icon: twitterIcon,
            url: 'https://twitter.com/RodrigoFavarete',
            tooltip: 'Twitter',
        },
        {
            icon: githubIcon,
            url: 'https://github.com/favarete',
            tooltip: 'Github',
        },
        {
            icon: mastodonIcon,
            url: 'https://mastodon.art/@favarete',
            tooltip: 'Mastodon',
        },
        {
            icon: hicetnuncIcon,
            url: 'https://www.hicetnunc.xyz/tz/tz1WQGhuPcsACPuTwxYJ2RVCkXiHJiNh462j',
            tooltip: 'Hicetnunc',
        },
        {
            icon: peepethIcon,
            url: 'https://peepeth.com/favarete',
            tooltip: 'Peepeth',
        },
        {
            icon: deviantartIcon,
            url: 'https://www.deviantart.com/favarete',
            tooltip: 'Deviantart',
        },
        {
            icon: elloIcon,
            url: 'https://ello.co/favarete',
            tooltip: 'Ello',
        },
        {
            icon: showtimeIcon,
            url: 'https://tryshowtime.com/favarete',
            tooltip: 'Showtime',
        },
        {
            icon: wtSocialIcon,
            url: 'https://wt.social/u/rodrigo-favarete',
            tooltip: 'WTSocial',
        }
    ];

    return (
        <IntroWrapper>
            <IntroImage>
                <Image fluid={AuthorImage} alt="author"/>
            </IntroImage>
            <IntroInfo>
                <IntroTitle>
                    I’m <b>{author}</b>
                </IntroTitle>
                <Description>{about}</Description>
                <SocialProfile items={SocialLinks}/>
            </IntroInfo>
        </IntroWrapper>
    );
};

export default Intro;

export const socialIconFragment = graphql`
    fragment SocialIconFragment on File {
        childImageSharp {
            fluid(maxWidth: 22, maxHeight: 22, quality: 100) {
                ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
        }
    }
`