import styled from 'styled-components';
import { themeGet } from '@styled-system/theme-get';

export const ContactWrapper = styled.div`
  width: 870px;
  margin: 0 auto;
  padding-top: 10px;
  max-width: 100%;
  @media (max-width: 990px) {
    padding: 10px 45px 30px 45px;
  }
  @media (max-width: 575px) {
    padding: 5px 25px 0 25px;
  }
`;

export const ContactPageContent = styled.p`
  font-size: ${themeGet('fontSizes.3')}px;
  line-height: ${themeGet('lineHeights.text')};
  font-weight: ${themeGet('fontWeights.3')};
  color: ${themeGet('colors.bodyText')};
  margin-bottom: 25px;
  @media (max-width: 1200px) {
    font-size: 14px;
  }
  @media (max-width: 575px) {
    font-size: 13px;
  }
`;

export const ContactFromWrapper = styled.div`
  position: relative;
  .button {
    margin-top: 60px;
    background-color: ${themeGet('colors.buttonLinks')};
    color: ${themeGet('colors.buttonLinksText')};
    &:hover {
      background-color: ${themeGet('colors.buttonLinksHover')};
    }
    @media (max-width: 575px) {
      margin-top: 40px;
    }
  }
`;

export const MagicInput = styled.div`
  position: absolute;
  left: -5000px;
`;

export const InputGroup = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  .field-wrapper {
    flex-grow: 1;
    max-width: calc(50% - 30px);
    margin-bottom: 60px;
    @media (max-width: 575px) {
      max-width: 100%;
      margin-bottom: 40px;
    }
  }
`;
