import * as React from 'react';
import Intro from './intro';
import {IoLogoFacebook, IoLogoInstagram, IoLogoLinkedin, IoLogoTwitter,} from 'react-icons/io';
import {AboutDetails, AboutWrapper,} from './style';
import Contact from "../contact";

export const SocialLinks = [
    {
        icon: <IoLogoFacebook/>,
        url: 'https://www.facebook.com/redqinc/',
        tooltip: 'Facebook',
    },
    {
        icon: <IoLogoInstagram/>,
        url: 'https://www.instagram.com/redqinc/',
        tooltip: 'Instagram',
    },
    {
        icon: <IoLogoTwitter/>,
        url: 'https://twitter.com/redqinc',
        tooltip: 'Twitter',
    },
    {
        icon: <IoLogoLinkedin/>,
        url: 'https://www.linkedin.com/company/redqinc/',
        tooltip: 'Linked In',
    },
];

interface AboutProps {
}

const About: React.FunctionComponent<AboutProps> = (props) => {
    return (
        <AboutWrapper {...props}>
            <Intro/>
            <AboutDetails>
                <h2>Hey there, what’s up?</h2>
                <p>
                    I think it is worth mentioning that my art is conceptual; I can't think only in aesthetics; I
                    need to have something meaningful to make everything make sense inside my head. This applies to
                    everything I create, from pieces of music and literature to games and tech ideas. Finally, I'm
                    starting to show my art to the world. It took most of my life to feel that my art was worth
                    noticing, so I hope my art makes you think and feel that you used your time well by looking at it.

                </p>
                <p>
                    Also, I'm a Vegan Straight Edge, and this is important. Everything in my art has that on its DNA,
                    from the materials I use to the content itself; my art will scream for animal rights, animal
                    liberation, environment, self-awareness, and self-respect since I'm consciously deciding to reject
                    the societal norm of self-intoxication. I also try to use my tech and scientific knowledge whatever
                    I can since that is a part of me; it's part of most of my life and formal career as a full stack
                    developer and data scientist.
                </p>
            </AboutDetails>
            <Contact/>
        </AboutWrapper>
    );
};

export default About;
