import * as React from 'react';
import {SocialProfileItem, SocialProfileWrapper, Tooltip,} from './social-profile.style';

type SocialProfileProps = {
    items: Socialitem[];
};

type Socialitem = {
    url: string;
    icon: string;
    tooltip: string;
};

const SocialProfile: React.FunctionComponent<SocialProfileProps> = (
    {
        items,
        ...props
    }
) => {
    return (
        <SocialProfileWrapper {...props}>
            {items.map((item, index) => (
                <SocialProfileItem key={index}>
                    <a href={item.url} target="_blank" aria-label="social profile">
                        <embed src={item.icon}/>
                    </a>
                    {item.tooltip != null && item.tooltip !== '' ? (
                        <Tooltip>{item.tooltip || 'Social Link'}</Tooltip>
                    ) : (
                        ''
                    )}
                </SocialProfileItem>
            ))}
        </SocialProfileWrapper>
    );
};

export default SocialProfile;
